import Vue from 'vue'
import Router from 'vue-router'
const login = () => import('./views/login')
const main = () => import('./views/main')
const comManage = () => import('./views/comManage/index')
const comList = () => import('./views/comManage/comList')
const infoRecheck = () => import('./views/comManage/infoRecheck/infoRecheck')
const comInfo = () => import('./views/comManage/infoRecheck/comInfo')
const goodsInfo = () => import('./views/comManage/infoRecheck/goodsInfo')
const serveInfo = () => import('./views/comManage/infoRecheck/serveInfo')
const addOrEditCom = () => import('./views/comManage/addOrEditCom')
const servesList = () => import('./views/comManage/servesList')
const addOrEditServe = () => import('./views/comManage/addOrEditServe')
const goods = () => import('./views/goodsManage/index')
const goodsManage = () => import('./views/goodsManage/goodsManage')
const addOrEditGoods = () => import('./views/goodsManage/addOrEditGoods')
const netHospital = () => import('./views/hosNetwork/hospital/hospital')
const hospitalAdd = () => import('./views/hosNetwork/hospital/controls/hosAdd')
const addDoc = () => import('./views/hosNetwork/doctor/controls/addDoc')
const netDoctor = () => import('./views/hosNetwork/doctor/doctor')
const netPhyExamination = () => import('./views/hosNetwork/phyExamination/phyExamination')
const addOrg = () => import('./views/hosNetwork/phyExamination/controls/addOrg')
const addSub = () => import('./views/hosNetwork/phyExamination/controls/addSub')
const netChMedicine = () => import('./views/hosNetwork/chMedicine/chMedicine')
const netDentistry = () => import('./views/hosNetwork/dentistry/dentistry')

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/',
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/',
      name: 'main',
      component: main,
      children: [
        {
          path: 'comManage',
          component: comManage,
          children: [
            {
              path: 'comList',
              component: comList
            },
            {
              path: 'infoRecheck',
              component: infoRecheck
            },
            {
              path: 'comInfo',
              component: comInfo
            },
            {
              path: 'goodsInfo',
              component: goodsInfo
            },
            {
              path: 'serveInfo',
              component: serveInfo
            },
            {
              path: 'addOrEditCom',
              component: addOrEditCom
            },
            {
              path: 'servesList',
              component: servesList
            },
            {
              path: 'addOrEditServe',
              component: addOrEditServe
            }
          ]
        },
        {
          path: 'hosNetwork',
          component: comManage,
          children: [
            {
              path: 'hospital',
              component: netHospital
            },
            {
              path: 'hospitalAdd',
              component: hospitalAdd
            },
            {
              path: 'doctor',
              component: netDoctor
            },
            {
              path: 'addDoc',
              component: addDoc
            },
            {
              path: 'phyExamination',
              component: netPhyExamination
            },
            {
              path: 'addOrg',
              component: addOrg
            },
            {
              path: 'addSub',
              component: addSub
            },
            {
              path: 'chMedicine',
              component: netChMedicine
            },
            {
              path: 'dentistry',
              component: netDentistry
            }
          ]
        },
        {
          path: 'goodsManage',
          component: goods,
          children: [
            {
              path: '/',
              component: goodsManage
            },
            {
              path: 'addOrEditGoods',
              component: addOrEditGoods
            }
          ]
        }
      ]
    }
  ]
})
